@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  background-color: #232533;
}

.App {
  text-align: center;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  color: white;
}

* {
  color: white;
  text-align: center;
  font-family: "Roboto", sans-serif;
  color: white;
}

body {
  margin: unset;
  overflow-x: hidden;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-thumb {
  background-color: #3292ff;
}

::-webkit-scrollbar-track {
  background-color: #282a39;
}

#header {
  position: absolute;
  height: 90px;
  top: 0;
  width: 100vw;
}

#header-container {
  display: flex;
  flex-direction: row;
  padding-left: 112px;
  padding-right: 152px;
  width: 100%;
  height: 100%;
  align-items: center;
  box-sizing: border-box;
  justify-content: space-between;
}

#logo {
  height: 30px;
}

#header-container > ul {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
  margin: unset;
}

#header-container > ul > a {
  padding: 0 17px;
  transition: color 250ms;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

#header-container > ul > a.selected {
  color: #3292ff;
}

.arrow-down {
  transition: fill 250ms;
}

#header-container > ul > a:hover .arrow-down {
  fill: #3292ff;
}

#header-container > ul > a.selected.scrolled {
  border-bottom: 2px solid #3292ff;
}

#header-container > ul > a:hover {
  color: #3292ff;
  cursor: pointer;
}

a > span {
  color: inherit;
}

#home {
  display: flex;
  flex-direction: column;
  padding-top: 224px;
  grid-gap: 2rem;
  gap: 2rem;
}

.content-section {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.info-card {
  display: flex;
  flex-direction: column;
  grid-gap: 2rem;
  gap: 2rem;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.info-card > .title {
  font-size: 1.8rem;
}

.info-card > .content {
  box-shadow: 0px 0px 20px 10px rgba(0, 0, 0, 0.25);
  padding: 25px;
  height: 470px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

#motd {
  min-width: 400px;
  max-width: 600px;
}

li {
  text-align: left;
}

.info-card > .content > h2 {
  margin: unset;
}

.pick-container {
  padding: unset !important;
}

#player-page {
  display: flex;
  flex-direction: column;
  padding-top: 224px;
  grid-gap: 2rem;
  gap: 2rem;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#player-container {
  width: 1100px;
}

#search {
  display: flex;
  flex-direction: column;
  padding-top: 224px;
  grid-gap: 2rem;
  gap: 2rem;
}

input {
  background-color: black;
}
